y: block;\n    background-color: #fff;\n    opacity: 0.6;\n    -webkit-transform-origin: center;\n    -ms-transform-origin: center;\n    transform-origin: center;\n    -webkit-transform: rotate(45deg);\n    -ms-transform: rotate(45deg);\n    transform: rotate(45deg);\n    -webkit-transition: 0.3s;\n    -o-transition: 0.3s;\n    transition: 0.3s;\n}\n\n.close-btn.close-dark span {\n    background-color: #1D2228;\n}\n\n.close-btn.close-danger span {\n    background-color: red;\n}\n\n.close-btn span:nth-child(2) {\n    -webkit-transform: rotate(-45deg);\n    -ms-transform: rotate(-45deg);\n    transform: rotate(-45deg);\n}\n\n.close-btn:hover span {\n    -webkit-transform: rotate(90deg);\n    -ms-transform: rotate(90deg);\n    transform: rotate(90deg);\n}\n\n.close-btn:hover span:nth-child(2) {\n    -webkit-transform: rotate(-90deg);\n    -ms-transform: rotate(-90deg);\n    transform: rotate(-90deg);\n}\n\n.close-btn.dark span {\n    background-color: #1D2228;\n}\n\n.icon-2x {\n    font-size: 2rem;\n}\n\n.icon-3x {\n    font-size: 3rem;\n}\n\n.icon-4x {\n    font-size: 4rem;\n}\n\n.border-0 {\n    border: 0;\n}\n\n.bg-cover {\n    background-size: cover;\n}\n\n.bg-parallax {\n    background-attachment: fixed;\n}\n\n.bg-contain {\n    background-size: contain;\n}\n\n.bg-norepeat {\n    background-repeat: no-repeat;\n}\n\n.bg-center {\n    background-position: center;\n}\n\n\n/*======================\n1.1. Typography\n========================*/\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n    margin: 0 0 20px;\n    font-family: 'Oswald', serif;\n    font-weight: 700;\n    line-height: 28px;\n    color: #1D2228;\n}\n\nh1 {\n    font-size: 50px;\n    line-height: 58px;\n}\n\nh2 {\n    font-size: 40px;\n    line-height: 48px;\n}\n\nh3 {\n    font-size: 35px;\n    line-height: 43px;\n}\n\nh4 {\n    font-size: 26px;\n    line-height: 32px;\n}\n\nh5 {\n    font-size: 18px;\n}\n\nh6 {\n    font-size: 16px;\n}\n\np {\n    font-size: 15px;\n    line-height: 28px;\n    margin-bottom: 15px;\n    color: #515151;\n}\n\na {\n    color: #ED4E53;\n    text-decoration: none;\n    -webkit-transition: .3s;\n    -o-transition: .3s;\n    transition: .3s;\n}\n\na:hover,\na:focus {\n    color: #da464a;\n    text-decoration: none;\n    -webkit-transition: 0.5s;\n    -o-transition: 0.5s;\n    transition: 0.5s;\n    outline: none;\n}\n\nb,\nstrong {\n    font-weight: 700;\n}\n\nlabel {\n    margin-bottom: 10px;\n    font-weight: 600;\n    color: #1D2228;\n}\n\nol,\nul {\n    list-style: none;\n    padding: 0;\n    margin-top: 0;\n    margin-bottom: 20px;\n}\n\nul li,\nol li {\n    margin: 10px 0 0;\n    position: relative;\n}\n\n.ct-list {\n    margin-bottom: 20px;\n}\n\n.ct-list li {\n    position: relative;\n    margin-top: 10px;\n    padding-left: 25px;\n}\n\n.ct-list li::before {\n    font-family: \"Font Awesome 5 Free\";\n    content: \"\\f058\";\n    -webkit-font-smoothing: antialiased;\n    font-style: normal;\n    font-variant: normal;\n    font-weight: 900;\n    text-rendering: auto;\n    line-height: 28px;\n    font-size: 14px;\n    position: absolute;\n    left: 0;\n    top: 50%;\n    -webkit-transform: translateY(-50%);\n    -ms-transform: translateY(-50%);\n    transform: translateY(-50%);\n    color: #1ec1b0;\n}\n\n.ct-list-decimal {\n    list-style: decimal;\n    padding-left: 15px;\n}\n\n.small,\nsmall {\n    line-height: 21px;\n}\n\nblockquote {\n    position: relative;\n    background-color: #fff;\n    position: relative;\n    padding: 30px;\n    margin: 20px 0;\n    border-left: 3px solid #ED4E53;\n    font-size: 18px;\n    font-family: \"Oswald\", sans-serif;\n}\n\nblockquote::before {\n    content: '\\f103';\n    font-family: flaticon;\n    position: absolute;\n    top: 50%;\n    left: -10px;\n    width: 20px;\n    height: 40px;\n    display: -webkit-box;\n    display: -ms-flexbox;\n    display: flex;\n    -webkit-box-align: center;\n    -ms-flex-align: center;\n    align-items: center;\n    -webkit-box-pack: center;\n    -ms-flex-pack: center;\n 