olor: #ff4500;\n    color: #fff;\n}\n\n.light-bg {\n    background-color: #f9f9f9;\n}\n\n\n/* Badges */\n\n[class*='badge-'] {\n    position: relative;\n    color: #fff;\n    font-weight: 400;\n    padding: 6px 9px;\n    font-size: 13px;\n}\n\n.badge.badge-primary {\n    background-color: #ED4E53;\n}\n\n.badge.badge-secondary {\n    background-color: #aa66cc;\n}\n\n.badge.badge-success {\n    background-color: #1ec1b0;\n}\n\n.badge.badge-warning {\n    background-color: #fda600;\n}\n\n.badge.badge-danger {\n    background-color: #ff737b;\n}\n\n.badge.badge-info {\n    background-color: #5a9ee0;\n}\n\n\n/*======================\n1.6 Modals & Popups\n========================*/\n\n.modal-lg {\n    max-width: 1000px;\n}\n\n.modal-header {\n    -webkit-box-align: center;\n    -ms-flex-align: center;\n    align-items: center;\n}\n\n.modal-header .close:not(:disabled):not(.disabled):hover {\n    background-color: #dd3333;\n    opacity: 1;\n}\n\n.modal-content {\n    border-radius: 8px;\n    border: 0;\n}\n\n.modal-header.modal-bg {\n    position: relative;\n    padding: 120px 0;\n    background-size: cover;\n    background-position: center;\n    border: 0;\n    border-radius: 8px 8px 0 0;\n    -webkit-box-pack: end;\n    -ms-flex-pack: end;\n    justify-content: flex-end;\n    -webkit-box-align: start;\n    -ms-flex-align: start;\n    align-items: flex-start;\n}\n\n.modal-header.modal-bg .close-btn {\n    position: absolute;\n    top: 20px;\n    right: 20px;\n}\n\n.modal-body {\n    padding: 20px;\n}\n\n\n/*======================\n1.7 Tables\n========================*/\n\ntable th,\ntable td {\n    padding: 15px;\n    vertical-align: middle;\n}\n\ntable th {\n    font-weight: 600;\n    color: #fff;\n    background-color: #1D2228;\n    font-family: \"Oswald\", sans-serif;\n}\n\ntable {\n    width: 100%;\n    margin-bottom: 30px;\n}\n\ntable td {\n    background-color: #f9f9f9;\n    border-bottom: 1px solid rgba(0, 0, 0, .1);\n}\n\ntable img {\n    width: 40px;\n    border-r